/* portfolio item */
.overlay-box {
  position: absolute;
  text-align: center;
  left: 0;
  height: 100%;
  width: 100%;
  top: 0;
}

.overlay-box::before {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background: #1f1f40;
  left: 0;
  top: 0;
  transform: scaleX(0);
  transform-origin: left;
  transition: .2s ease-in;
}

.overlay-box::after {
  position: absolute;
  content: "";
  height: 100%;
  width: 100%;
  background: #f57479;
  left: 0;
  top: 0;
  transform: scaleX(0);
  transform-origin: left;
  transition: .2s ease-in;
}

.overlay-box .overlay-inner {
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

.overlay-box .overlay-inner h5 {
  transform: translateX(-40px);
  opacity: 0;
  transition: .2s ease;
  transition-delay: .0s;
}

.overlay-box .overlay-inner p {
  color: #fff;
  transform: translateX(-16px);
  opacity: 0;
  transition: .2s ease;
  transition-delay: .0s;
}

.inner-box:hover .overlay-box::before {
  transform: scaleX(1);
}

.inner-box:hover .overlay-box::after {
  transform: scaleX(1);
  transition-delay: .3s;
}

.inner-box:hover .overlay-inner h5 {
  transform: translateX(0);
  opacity: 1;
  transition-delay: .4s;
}

.inner-box:hover .overlay-inner p {
  transform: translateX(0);
  opacity: 1;
  transition-delay: .5s;
}

